<template>
  <div>
    <h1 class="subtitle-1 grey--text">This is Home page</h1>
    <v-container class="my-5 mx-auto pa-0">
      <v-layout row class="mb-5">
        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              small
              depressed
              class="transparent grey--text"
              @click="sortBy('title')"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon small left>mdi-folder</v-icon>
              <span class="text-capitalize">By project name</span>
            </v-btn>
          </template>
          <span>Sorts projects by title</span>
        </v-tooltip>

        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              small
              depressed
              class="transparent grey--text"
              @click="sortBy('person')"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon small left>mdi-account</v-icon>
              <span class="text-capitalize">By person</span>
            </v-btn>
          </template>
          <span>Sorts projects by name</span>
        </v-tooltip>
      </v-layout>

      <v-card
        flat
        class="white"
        v-for="project in projects"
        :key="project.title"
      >
        <v-layout row wrap :class="`ma-auto pa-3 project ${project.status}`">
          <v-flex xs12 md6>
            <div class="caption grey--text">Project title</div>
            <div>{{ project.title }}</div>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <div class="caption grey--text">Person</div>
            <div>{{ project.person }}</div>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <div class="caption grey--text">Due by</div>
            <div>{{ project.dueBy }}</div>
          </v-flex>
          <v-flex xs2 sm4 md2>
            <div class="d-flex justify-end">
              <v-chip id="chip" :class="`white--text ${project.status} caption`"
                >{{ project.status }}
              </v-chip>
            </div>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
      </v-card>
      <!-- add project button -->
      <v-layout row wrap class="mt-5 d-flex justify-end">
        <v-flex xs2 sm2 md2 class="d-flex justify-end">
          <v-dialog
            v-model="dialog"
            scrollable
            persistent
            :overlay="false"
            max-width="500px"
            transition="dialog-transition"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="success" large v-bind="attrs" v-on="on">
                Add new project
              </v-btn>
            </template>
            <v-card class="grey lighten-4">
              <v-card-title primary-title class="justify-center grey--text">
                Add new project
              </v-card-title>
              <v-card-text>
                <v-form ref="form" class="px-3">
                  <v-text-field
                    label="Title"
                    prepend-icon="folder"
                    v-model="title"
                    :rules="inputRules"
                  >
                  </v-text-field>
                  <v-textarea
                    label="Information"
                    prepend-icon="edit"
                    v-model="info"
                    :rules="inputRules"
                  >
                  </v-textarea>

                  <v-col cols="12" sm="6" md="4">
                    <v-menu
                      transition="scale-transition"
                      offset-y
                      min-width="auto"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                          :value="date"
                          :rules="inputRules"
                          label="Pick date"
                          prepend-icon="mdi-calendar"
                          readonly
                          v-bind="attrs"
                          v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker
                        v-model="date"
                        no-title
                        scrollable
                      ></v-date-picker>
                    </v-menu>
                  </v-col>
                </v-form>
              </v-card-text>

              <v-card-actions class="justify-center">
                <v-btn color="success " text @click="submit()">
                  Add new project
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import database from "@/firebase.js";
export default {
  name: "Home-page",
  data() {
    return {
      dialog: false,
      title: "",
      info: "",
      date: "",
      inputRules: [(value) => value.length >= 3 || "Fill the input"],
      projects: [
        {
          title: "Design the website",
          person: "Iraj",
          dueBy: "2023-7-27",
          status: "complete",
        },
        {
          title: "Front-End",
          person: "Peyman",
          dueBy: "2023-7-28",
          status: "ongoing",
        },
        {
          title: "Back-End",
          person: "Fariba",
          dueBy: "2023-7-29",
          status: "complete",
        },
        {
          title: "Upload on server",
          person: "Behrooz",
          dueBy: "2023-7-30",
          status: "overdue",
        },
      ],
    };
  },
  methods: {
    sortBy(item) {
      this.projects.sort((a, b) => (a[item] < b[item] ? -1 : 1));
    },
    submit() {
      if (this.$refs.form.validate()) {
        const project = {
          title: this.title,
          info: this.info,
          date: this.date,
          person: "Pegah",
          status: "ongoing",
        };
        database.collection("projects").add(project);
        this.dialog = false;
      }
    },
  },

  components: {},
};
</script>

