<template>
  <nav>
    <v-toolbar app flat>
      <v-toolbar-title class="text-uppercase grey--text">
        <v-app-bar-nav-icon
          class="grey--text"
          @click="drawerChecker(), check()"
        >
          <burgerBtn v-bind:class="{ 'is-active': isActive }"></burgerBtn>
        </v-app-bar-nav-icon>
        <router-link
          class="d-inline-block grey--text text-decoration-none"
          router
          to="/"
        >
          <span class="font-weight-light">Todo</span>
          <span>Ninja</span>
        </router-link>
      </v-toolbar-title>

      <v-spacer></v-spacer>

      <v-menu transition="slide-x-transition" bottom offset-y>
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            depressed
            class="transparent grey--text"
            dark
            v-bind="attrs"
            v-on="on"
          >
            <v-icon left>mdi-chevron-down</v-icon>
            Menu
          </v-btn>
        </template>
        <v-list>
          <v-list-item
            style="color: grey"
            v-for="link in links"
            :key="link.text"
            route
            :to="link.route"
          >
            <v-list-item-title>
              {{ link.text }}
            </v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>

      <v-btn depressed color="transparent grey--text">
        <span>sign out</span>
        <v-icon right>mdi-exit-to-app</v-icon>
      </v-btn>
    </v-toolbar>
    <v-navigation-drawer
      disable-resize-watcher
      floating
      app
      left
      v-model="drawer"
      color="grey lighten-4"
    >
      <v-list>
        <v-list-item
          class="grey--text text-uppercase"
          v-for="link in links"
          :key="link.text"
          router
          :to="link.route"
        >
          <v-list-item-action>
            <v-icon left class="grey--text" style="margin-left: 5px">
              {{ link.icon }}</v-icon
            >
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title class="d-flex justify-start">
              <span>{{ link.text }}</span>
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </nav>
</template>
<script>
import burgerBtn from "./burger-btn.vue";
export default {
  data() {
    return {
      drawer: false,
      isActive: false,
      windowWidth: window.innerWidth,
      selected: false,
      links: [
        { icon: "mdi-home", text: "Home", route: "/" },
        { icon: "mdi-exit-to-app", text: "About", route: "/about" },
        { icon: "mdi-account-supervisor-circle", text: "Team", route: "/team" },
      ],
    };
  },
  methods: {
    drawerChecker() {
      this.drawer = !this.drawer;
    },
    check() {
      this.isActive = !this.isActive;
    },
  },
  components: {
    burgerBtn,
  },
  watch: {
    drawer() {
      if (!this.drawer && this.windowWidth <= "1264") {
        this.isActive = !this.isActive;
      }
    },
  },
  mounted() {
    window.addEventListener("resize", () => {
      this.windowWidth = window.innerWidth;
    });
  },
};
</script>
<style>
.v-list-item--active {
  color: purple !important;
  text-align: center;
}
</style>
